function Sketch(p) {
    let grid = []
    let cols, rows
    let w = 20
    let current;
    let stack = []

    p.setup = function() {      
        p.createCanvas(1000, 1000);
        p.translate(0,500)
       
        cols = p.floor(p.width/w);
        rows = p.floor(p.height/w);
        
        for(var i=0; i<rows; i++) {
          for(var j=0; j<cols; j++) {
            var cell = new Cell(i,j);
            grid.push(cell);
          }
        }
        p.frameRate(100)
        current = grid[0]
        
      }

    p.draw = function() {
        p.background(100);
        for(var i=0; i<grid.length; i++) {
          grid[i].show();
        }
        
        current.visited = true;
        current.highlight();
        let next = current.checkneighbors();
        if(next) {
          next.visted = true;
          stack.push(current)
          removeWalls(current, next)
          current = next;
        } else {
          if(stack.length > 0) {
            current = stack.pop()
          }
        }
        
      }

      function index(i, j) {
  
        if(i < 0 || j < 0 || i > rows-1 || j > cols-1 )
          return -1;
        
        return j + i*cols;
      }
      
      function removeWalls(a, b) {
        var x = a.i - b.i;
        var y = a.j - b.j;
        
        if(x === 1) {
          a.walls[3]= false
          b.walls[1] = false
        } else if(x === -1) {
          a.walls[1] = false;
          b.walls[3] = false;
        }
        
       if(y === 1) {
          a.walls[0]= false
          b.walls[2] = false
        } else if(y === -1) {
          a.walls[2] = false;
          b.walls[0] = false;
        }
        
      }

      function Cell(i,j) {
        this.i = i
        this.j = j
        this.walls = [true, true, true, true]
        this.visited = false;
        this.checkneighbors = function() {
          var neighbors = []
          
          var top = grid[index(i,j-1)]
          var right = grid[index(i+1,j)]
          var bottom = grid[index(i,j+1)]
          var left= grid[index(i-1,j)]
          
          if(top && !top.visited) 
            neighbors.push(top)
          if(right && !right.visited)
            neighbors.push(right)
          if(bottom && !bottom.visited)
            neighbors.push(bottom)
          if(left && !left.visited)
            neighbors.push(left)
          
          if(neighbors.length > 0) {
            var r = p.floor(p.random(0,neighbors.length))
            return neighbors[r]
          } else {
            return undefined;
          }
          
      
        }
        
        this.highlight = function() {
          var x = this.i*w;
          var y = this.j*w;
          p.noStroke();
          p.fill(0,0,255,100)
          p.rect(x,y,w,w)
        }
        
        this.show = function() {
          var x = this.i*w;
          var y = this.j*w;
         
          
          p.stroke(255)
         // noFill()
         // rect(x,y,w,w);
          
          if(this.walls[0]) 
            p.line(x,y,x+w,y)
          if(this.walls[1]) 
            p.line(x+w,y,x+w,y+w)
          if(this.walls[2]) 
            p.line(x+w,y+w,x,y+w)
          if(this.walls[3]) 
            p.line(x,y+w,x,y)
          
           if(this.visited) {
             p.noStroke()
             p.fill(255,0,255,10)
             p.rect(x,y,w,w)
           }
          
        }
        
      }

}

export default Sketch;