
function Fibonacci() {
    const code = String.raw`
    int fibo(int n) {
        if( n==0 || n==1) 
            return 1;
        return fibo(n-1) + fibo(n-2);
     }
    
     int fibo_dp_bottomup(int n) {
        /* Effective space utilization, instead of array, just maintain
           only 2 varaibles */
        int first = 1;
        int second = 1;
        int ans = 0;
        if (n==0 || n==1)
            return 1;
        for(int i=2; i&lt;=n; i++) {
            ans = second + first;
            first = second;
            second = ans;
        }
        return ans;
    }
    
    int fibo_dp_topdown(int n, int* dp) {
        // Memoization = Recursion + lookup table
        if( n==0 || n==1) {
            return 1;
        }
        if(dp[n]!= -1) {
            return dp[n];
        }
        int ans = fibo_dp_topdown(n-1, dp) +
                  fibo_dp_topdown(n-2, dp);
        dp[n] = ans;
        
        return ans;
    }
     `;
    return (
        <div className="cpp-container"> 
        <section  id="Fibonacci" className="cpp-container cpp-card">
          <pre className='prettyprint lang-cpp'>
          {code}
          </pre>
        </section>
        </div>
    );
  }
  
  export default Fibonacci;
  